.about {
    min-width: 100vw;
    min-height: 100vh;
    position: relative;
    display: grid;
    grid-template-areas:
        "h h h"
        "l m r";
    
    #credits {
        grid-area: m;
        height: fit-content;
        font-family: 'FilmCryptic';
        font-size: 20px;
        text-align: center;
        // Fond ardoise
        background-image: url("../../../public/img/fond_ardoise.png");
        margin-left:  13%;
        margin-right: 13%;
        border: 10px solid transparent;
        border-image: url("../../../public/img/fond_bois.png") 30 30 stretch;
        border-width: 13px;
        box-shadow: 7px 7px 15px black;
    
        h2 {
            margin-top: 1%;
        }
        
        img {
            height: 175px;
            width: auto;
            border-radius: 25px;
        }
    
        ul {
            padding: 0;
            text-align: center;
        }
    
        li {
            list-style-type: none;
        }
    
        .linkedin {
            height: 17px;
            width: auto;
            border-radius: 0px;
        }
    }
}